.card-container{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#faf0dc;border:1px solid grey;border-radius:5px;padding:25px;cursor:pointer;transition:transform 0.25s ease-out;transform:scale(1)}.card-container:hover{transform:scale(1.05)}

.card-list{width:85vw;margin:0 auto;display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-gap:20px 20px;gap:20px 20px}

.search{background-color:antiquewhite;border:none;outline:none;padding:10px;width:150px;line-height:30px;margin-bottom:30px}

.App{text-align:center}h1{font-family:'Bigelow Rules';font-size:72px;color:#f07e74}

body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(to left, #522307 0%, teal 100%)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

